.container {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
  background-color: #f4f4f4;
  min-height: 100vh;
}
.hero {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 2;
  background-color: #f4f4f4;
}
.hero h1 {
  font-size: 4rem;
  margin-bottom: 1rem;
}
.hero p {
  font-size: 1.5rem;
  color: #666;
}
.cardsSection {
  padding: 0 20px;
  max-width: 800px;
  margin: 0 auto;
  padding-bottom: 20vh;
  position: relative;
  z-index: 2;
  background-color: #f4f4f4;
}
.cardContainer {
  position: sticky;
  height: 400px;
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
}
.card {
  width: 100%;
  height: 100%;
  border-radius: 24px;
  padding: 40px;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  transform-origin: center top;
}
.card h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: white;
}
.card p {
  font-size: 1.2rem;
  opacity: 0.9;
}
.horizontalScrollSection {
  height: 300vh;
  position: relative;
  z-index: 2;
  background-color: #1a1a1a;
}
.horizontalStickyWrapper {
  position: sticky;
  top: 0;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #1a1a1a;
  color: #fff;
}
.horizontalHeader {
  position: absolute;
  top: 10%;
  left: 5vw;
  z-index: 10;
}
.horizontalHeader h2 {
  font-size: 3rem;
  color: #fff;
  margin-bottom: 1rem;
}
.horizontalHeader p {
  font-size: 1.2rem;
  color: #aaa;
}
.horizontalTrack {
  display: flex;
  gap: 60px;
  padding-left: 5vw;
}
.horizontalCard {
  flex: 0 0 400px;
  height: 500px;
  background: #2d2d2d;
  border-radius: 24px;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid #444;
}
.horizontalCard h3 {
  color: #fff;
  font-size: 1.8rem;
  margin-bottom: 1rem;
}
.horizontalCard .mockUI {
  width: 100%;
  height: 200px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  margin-top: 20px;
}
.stickyFeatureSection {
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  padding: 100px 20px;
  position: relative;
  z-index: 2;
  background: #fff;
}
.featureContent {
  width: 50%;
  padding-right: 60px;
}
.featureTextBlock {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  opacity: 0.2;
  transition: opacity 0.5s ease;
}
.featureTextBlock h2 {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  color: #000;
}
.featureTextBlock p {
  font-size: 1.2rem;
  line-height: 1.6;
  color: #555;
}
.featureTextBlock.active {
  opacity: 1;
}
.featureImages {
  width: 50%;
  height: 100vh;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.imageWrapper {
  position: absolute;
  width: 100%;
  height: 600px;
  border-radius: 24px;
  overflow: hidden;
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.15);
  background: #f0f0f0;
}
.imageWrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.framerMarqueeSection {
  padding: 100px 0;
  background: #050505;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 32px;
  position: relative;
  z-index: 2;
}
.framerMarqueeSection h2 {
  text-align: center;
  font-size: 3rem;
  margin-bottom: 40px;
  color: #fff;
}
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80vh;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.marqueeSection {
  padding: 100px 0;
  background: #fff;
  overflow: hidden;
  position: relative;
  z-index: 2;
}
.marqueeSection h2 {
  text-align: center;
  font-size: 3rem;
  margin-bottom: 60px;
  color: #000;
}
.resourceCard {
  width: 300px;
  height: 200px;
  background: #f5f5f5;
  border-radius: 16px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
  flex-shrink: 0;
}
.resourceCard:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}
.resourceCard img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.resourceCard .resourceInfo {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 16px;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.8), transparent);
  color: #fff;
}
.resourceCard .resourceInfo h4 {
  margin: 0;
  font-size: 1.1rem;
  color: #fff;
}
.resourceCard .resourceInfo span {
  font-size: 0.9rem;
  opacity: 0.8;
}
.marqueeRow {
  display: flex;
  width: 100%;
  overflow: visible;
}
.marqueeTrack {
  display: flex;
  gap: 32px;
  width: max-content;
  padding: 10px 0;
}
.marqueeCard {
  width: 280px;
  height: 360px;
  border-radius: 16px;
  overflow: hidden;
  position: relative;
  flex-shrink: 0;
  background: #1a1a1a;
  transition: transform 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  cursor: pointer;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}
.marqueeCard:hover {
  transform: scale(1.05);
  z-index: 10;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
}
.marqueeCard img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}
.marqueeCard:hover img {
  transform: scale(1.1);
}
.marqueeCard .cardContent {
  position: absolute;
  inset: 0;
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 2;
}
.marqueeCard .cardContent h3 {
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1.1;
  margin-bottom: 12px;
}
.marqueeCard .cardContent p {
  font-size: 1rem;
  opacity: 0.8;
  line-height: 1.4;
}
.marqueeCard .cardContent .btn {
  align-self: flex-start;
  padding: 8px 20px;
  border-radius: 20px;
  background: #fff;
  color: #000;
  font-weight: 600;
  font-size: 0.9rem;
  margin-top: auto;
}
.marqueeCard.card-text-white {
  background: #fff;
  color: #000;
}
.marqueeCard.card-text-white .cardContent .btn {
  background: #000;
  color: #fff;
}
.marqueeCard.card-text-dark {
  background: #111;
  color: #fff;
}
.marqueeCard.card-gradient-1 {
  background: linear-gradient(135deg, #ff4d4d, #f9cb28);
  color: #fff;
}
.marqueeCard.card-gradient-2 {
  background: linear-gradient(135deg, #7928ca, #ff0080);
  color: #fff;
}
.marqueeCard.card-gradient-3 {
  background: linear-gradient(135deg, #007cf0, #00dfd8);
  color: #fff;
}
.marqueeCard.card-gradient-4 {
  background: linear-gradient(135deg, #4353ff, #90caf9);
  color: #fff;
}
.catButtonWrapper {
  position: relative;
  display: inline-block;
  z-index: 1;
  margin-top: 40px;
}
.catBtn {
  position: relative;
  background: #000;
  color: #fff;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 16px 48px;
  border-radius: 100px;
  border: none;
  cursor: pointer;
  z-index: 2;
  transition: transform 0.2s ease;
}
.catBtn:hover {
  transform: scale(1.05);
}
.catPaw {
  position: absolute;
  top: -35px;
  right: 20px;
  width: 40px;
  height: 40px;
  z-index: 1;
  pointer-events: none;
}
.catPaw svg {
  width: 100%;
  height: 100%;
  fill: #333;
}
